import Vue from 'vue'
import App from './App.vue'
import store from './store'
import doui from './doui'
import i18n from './assets/common/i18n';
import './mock'
import DatePicker from 'vue2-datepicker'
import Bus from './bus'

Vue.config.productionTip = false

Vue.prototype.$kalendar = {};
Vue.prototype.$bus = Bus

Vue.use(doui);
Vue.use(DatePicker);

const agent = navigator.userAgent.toLowerCase();
// 判断mock环境
console.log('mock environment:', agent);
if (agent.indexOf('electron') !== -1) {
  // 配置electron下的环境变量
  let service = window.require('electron').remote.getGlobal('service');
  i18n.locale = service.config.getLanguage();
}

new Vue({
  i18n,
  store,
  render: h => h(App)
}).$mount('#app')
