<template>
  <div class="part-right">
    <!-- <d-button class="week-button" @click="shoWeekClick">周视图</d-button> -->
    <Calendar size="large" class="calendar" @pickDay='pickedDay' :setCalendar='setCalendar'>
    </Calendar>
  </div>
</template>
<script>
import Calendar from "../Calendar/Calendar.vue";

export default {
  name:"part-right",
  components:{
    Calendar
  },
  data(){
    return {
      setCalendar:{
        setCalendarDate: '',
        isSetCalendar:true
      }
    }
  },
  methods:{
    pickedDay(time,data) {
      console.log(time,data)
      data.forEach(element => {
        if(element.picked == true){
          this.$emit('shoWeek',element,time)
          console.log('picked')
        }
      });
    },
    shoWeekClick() {
      this.$emit('shoWeek')
    }
  }
}
</script>
<style lang="scss" scoped>
  .part-right{
  user-select: none;
  position: relative;
  flex: 1;
  .week-button{
    position: absolute;
    top: 28px;
    right: 137px;
    z-index: 2;
  }
}
</style>