<template>
  <div>
    <d-icon icon='d-icon-back' class="calender-button" @click="returnEvent" color='#757575'></d-icon>
    <kalendar
      :configuration.sync="calendar_settings"
      :events.sync="events"
      :key="kalendarRenderId"
      :clickDay='clickDate'
      class="generate-shadow"
    >
      <div slot="created-card" slot-scope="{ event_information }" class="details-card">
        <h4 class="appointment-title">{{event_information.data.title}}</h4>
        <small>{{event_information.data.description}}</small>
        <span class="time">
          {{event_information.start_time | formatToHours}} - {{event_information.end_time |
          formatToHours}}
        </span>
        <button @click="removeEvent(event_information)" class="remove">X</button>
      </div>

      <div slot="creating-card" slot-scope="{ event_information }">
        <h4 class="appointment-title" style="text-align: left;"></h4>
        <span class="time">
          {{event_information.start_time | formatToHours}}
          -
          {{event_information.end_time | formatToHours}}
        </span>
      </div>

      <div
        slot="popup-form"
        slot-scope="{ popup_information }"
        style="display: flex; flex-direction: column;"
      >
        <!-- <h4 style="margin-bottom: 10px">New Appointment</h4> -->
        <input v-model="new_appointment['title']" type="text" name="title" placeholder="Title" />
        <textarea
          v-model="new_appointment['description']"
          type="text"
          name="description"
          placeholder="Description"
          rows="2"
        ></textarea>
        <div class="buttons">
          <button class="cancel" @click="closePopups()">{{$t('remind.cancel')}}</button>
          <button @click="addEvent(popup_information)">{{$t('remind.confirm')}}</button>
        </div>
      </div>
    </kalendar>
  </div>
</template>
<script>
import Kalendar from "./kalendar/kalendar-container";
import { DateTime } from "luxon";

// let today = new Date();
// let year = today.getFullYear() + "",
//    month = (today.getMonth() + 1 + "").padStart(2, 0),
//      day = (today.getDate() + "").padStart(2, 0);
// const currentDay = `${year}-${month}-${day}T00:00:00.000Z`;

// if(this.clickDate != undefined){
  //   let year = this.clickDate.getFullYear() + '',
  //       month = (this.clickDay.getMonth() + 1 + '').padStart(2, 0),
  //       day = (this.clickDay.getDate() + '').padStart(2, 0)
  //   const currentDay = `${year}-${month}-${day}T00:00:00.000Z`;
  // }else {
  // }
export default {
  props:['clickDate'],
  filters: {
    formatToHours(value, how) {
      let dt = DateTime.fromISO(value);
      return dt.toLocaleString(DateTime.TIME_24_SIMPLE);
    }
  },
  components: {
    Kalendar
  },
  created(){
    console.log(this.clickDate)
  },
  watch:{},
  computed: {
    kalendarRenderId() {
      let {
        view_type,
        scrollToNow,
        current_day,
        read_only,
        day_starts_at,
        day_ends_at,
        overlap,
        hide_dates,
        hide_days,
        past_event_creation
      } = this.calendar_settings;
      let arr = [
        view_type,
        scrollToNow,
        current_day,
        read_only,
        day_starts_at,
        day_ends_at,
        overlap,
        hide_dates,
        hide_days,
        past_event_creation
      ];
      console.log({ arr });
      return arr.join("-");
    }
  },
  data() {
    return {
      events: [],
      calendar_settings: {
        view_type: "day",
        cell_height: 20,
        scrollToNow: false,
        current_day: this.setTime(),
        style: "material_design",
        military_time: false,
        read_only: false,
        day_starts_at: 0,
        day_ends_at: 24,
        overlap: false,
        hide_dates: ["2019-10-31"],
        // hide_days: [6],
        past_event_creation: true
      },
      outline_slots: false,
      new_appointment: {},
      adding_manually: false,
      input_value: ""
    };
  },

  methods: {
    getHours(start, end) {
      return `${format(start, "hh:mm A")} - ${format(end, "hh:mm A")}`;
    },
    addEvent(popup_info) {
      let payload = {
        data: {
          title: this.new_appointment.title,
          description: this.new_appointment.description
        },
        from: popup_info.start_time,
        to: popup_info.end_time
      };

      this.$kalendar.addNewEvent(payload);
      this.$kalendar.closePopups();
      this.clearFormData();
    },
    closePopups() {
      this.$kalendar.closePopups();
    },
    clearFormData() {
      this.new_appointment = {
        description: null,
        title: null
      };
    },
    addManually(payload) {
      this.$kalendar.addNewEvent(payload);
      this.adding_manually = false;
    },
    removeEvent(kalendarEvent) {
      console.log({ kalendarEvent });
      let day = kalendarEvent.start_time.slice(0, 10);
      this.$kalendar.removeEvent({
        day,
        key: kalendarEvent.key,
        id: kalendarEvent.kalendar_id
      });
    },
    returnEvent() {
      this.$emit('goBack')
    },
    setTime() {
    //  let year = this.clickDate.getFullYear() + '',
    //      month = this.clickDay.getMonth() + 1 + '',
    //       day = this.clickDay.getDate() + ''
      // return `${year}-${month}-${day}`;
      console.log('this.clickDate',this.clickDate.getMonth()+1)
      let year = this.clickDate.getFullYear() ;
      let month = (this.clickDate.getMonth() + 1 + '').padStart(2, 0);
      let day = (this.clickDate.getDate() + '').padStart(2, 0);
      console.log(`${year}-${month}-${day}`)
       return `${year}-${month}-${day}`
    }
  }
};
</script>
<style lang="scss">
  .calender-button {
    color: rgb(177, 175, 175);
    position: absolute !important;
    top: 18px;
    right: 35px;
  }
</style>