<template>
  <div id="app">
    <remind-me @shoWeekPage='shoWeekPage' @toAppValue='toAppValue'></remind-me>
    <Calender v-if="showWeekCalender" class="calender-page" @goBack='showWeekCalender = false' :calenderValue='toCalenderValue' :clickDate='clickDate'></Calender>
  </div>
</template>

<script>
import RemindMe from "./components/RemindMe/RemindMe.vue";
import Calender from "./components/kalenderIndex.vue"

export default {
  components: {
    RemindMe,
    Calender
  },
  data() {
    return {
      showWeekCalender:false,
      toCalenderValue:[],
      clickDate:''
    }
  },
  computed: {
    kalendarRenderId() {
      let { view_type, scrollToNow, current_day, read_only, day_starts_at, day_ends_at, overlap, hide_dates, hide_days, past_event_creation } = this.calendar_settings;
      let arr = [view_type, scrollToNow, current_day, read_only, day_starts_at, day_ends_at, overlap, hide_dates, hide_days, past_event_creation];
      console.log({arr});
      return arr.join('-');
    },
  },
  created() {
    console.log("get imsdkservice: ", this);
    console.log("i get env: ", process.env.NODE_ENV);
    if (process.env.NODE_ENV === "development") {
      let loginParam = {
        user: "008618340828615", // 用户名 **手机号要加 0086**
        pwd: "123123", // 密码
        userType: 1, // 用户类型,不修改
        server: "vrv", // 服务器,不修改
        nation: "0086" // 国家,不修改
      };
      this.$Service.auth.login(loginParam, resp => {
        console.log("i get login result: ", resp);
        // 0 成功 113 账号已登录
        if (resp.code === 0 || resp.code === 113 || resp.code === 100001575) {
          // 登录成功 跳转路由
          console.log('登录是否成功',resp)
        }
      });
    }
  },
  methods: {
    toAppValue(val) {
      console.log('appval',val);
      this.toCalenderValue = val;
    },
    shoWeekPage(time){
      this.showWeekCalender = true;
      this.clickDate = time
    }
  },
};
</script>
<style lang="scss">
html,body, #app {
  height: 100%;
}
* {
  margin: 0px;
  padding: 0px;
  &::-webkit-scrollbar {
    width: 6px; /*高宽分别对应横竖滚动条的尺寸*/
  }
  &::-webkit-scrollbar-thumb {
    height: 5px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #c0c4cc;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #ededed;
  }
}
.calender-page {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3
}
</style>
