import Service from './service';

export default class EnterpriseService extends Service {
  constructor() {
    super('enterprise');
  }

  getVisibleOrgUsers(param, cb) {
    this._process('getVisibleOrgUsers', param, cb);
  }

  getNote(param, cb) {
    this._process('getNote', param, cb);
  }

  searchNote(param, cb) {
    this._process('searchNote', param, cb);
  }

  delNote(param, cb) {
    this._process('delNote', param, cb);
  }

  addNote(param, cb) {
    this._process('addNote', param, cb);
  }
}
