<template>
  <div class="part-left">
    <div class="remind-list">
      <div class="backlog-title">{{$t('remind.reminderList')}}</div>
      <div class="backlog-list" ref="remind-list">
        <template v-if="msgRemindList.length > 0">
          <remind-item  v-for="(item,index) in msgRemindList" :key="index" :item="item" :index="index" @deleteRemind="deleteRemind"></remind-item>
        </template>
        <template v-else>
          <div class="empty-list" :style="{backgroundImage: emptyImg}">
            <img class="empty-img" :src="emptyImg" @dragstart.prevent>
          </div>
        </template>
      </div>
    </div>
    <div class="confirm-delete" v-if="showCofirmDelete">
      <d-card class="confirm-delete-card" :body-style="{ padding: '0px' }" >
        <div slot="header" class="delete-header">
          {{$t('remind.whetherToDelete')}}
        </div>
        <div class="delete-button">
          <d-button type="danger" class="delete-cancel" @click="cancel">{{$t('remind.cancel')}}</d-button>
          <d-button class="delete-confirm" @click='confirm'>{{$t('remind.confirm')}}</d-button>
        </div>
      </d-card>
    </div>
  </div>
</template>
<script>
const emptyImages = require('assets/images/nocontent.png');
import Calendar from "../Calendar/Calendar";
import RemindItem from './RemindItem';
export default {
  name:'part-left',
  components:{
    Calendar,
    RemindItem
  },
  data() {
    return {
      msgRemindList:{},
      listIndex: "",
      listId:'',
      showCofirmDelete:false,
      emptyImg: emptyImages,
    };
  },
  created() {
    let param = {
      pageNo: 1,
      pageSize: 10,
    }
    this.$Service.chat.getReminderMsgByPage(param, resp =>{
      console.log("获取数据：：：:",resp)
      this.msgRemindList = resp.reminderMsgs
      this.$store.commit('RemindListValue', this.msgRemindList)
      this.$emit('ViewDetails',this.msgRemindList)
    })
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs['remind-list'].addEventListener('scroll', (e) => {
        e.stopPropagation();
        this.loadMoreData(this.$refs['remind-list']);
      })
    });
  },
  methods:{
    deleteRemind({Id,index}){
      this.showCofirmDelete = true;
      this.listIndex = index;
      this.listId = Id
    },
    cancel(){
      this.showCofirmDelete = false;
      console.log('quxiao')
    },
    confirm() {
      this.$Service.chat.deleteReminderMsg({reminderId:this.listId},resp => {
        console.log('删除结果',resp);
        console.log(this.msgRemindList)
        if (resp.code === 0 || resp.code === 100013002 ) {
          this.msgRemindList.splice(this.listIndex, 1);
        }
      })
      this.showCofirmDelete = false;
    },
    // 滑动加载更多
    loadMoreData(el) {
      this.$nextTick(() => {
        if (this.isScrollToBottom(el)){
          console.log('到底了');
          // 处理逻辑
        }
      })
    },
    /**
     * 判断滚动条滚动到底部
     * @param {*} el - 绑定的dom元素
     * @returns - 是/否
     */
    isScrollToBottom(el) {
      let [scrollTop, offsetHeight, scrollHeight] = [el.scrollTop, el.offsetHeight, el.scrollHeight];
      return !(scrollTop + offsetHeight < scrollHeight)
    },
  }
}
</script>
<style scoped lang="scss">
.part-left {
  box-sizing: border-box;
  flex-basis: 240px;
  height: 100%;
  border-right: 1px solid #DCDFE6;
  transition: .3s;
  .calendar {
    user-select: none;
    height: 184px;
  }
  .remind-list {
    width: 100%;
    height: 100%;
    .backlog-title {
      box-sizing: border-box;
      padding: 10px;
      width: 100%;
      height: 40px;
      font-size: 14px;
      font-weight: bold;
      color: #303133;
      background-color: #EFF0F1;
      user-select: none;
      border-left: 6px solid #3370FF;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .backlog-list {
      overflow: hidden;
      height: calc(100% - 40px);
      padding: 10px;
      background-color: #F8F9FA;
      &:hover{
       overflow: overlay;
      }
      .empty-list {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .empty-img {
          width: 30%;
          user-select: none;
        }
      }
    }
  }
  .confirm-delete{
    position: absolute;
    top: 0px;
    z-index: 10;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0.6);
    .confirm-delete-card {
      box-sizing: border-box;
      width: 300px;
      height: 127px;
      margin: auto;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      .delete-header{
        height: 45px;
        line-height: 45px;
      }
      .delete-button{
        display: flex;
        justify-content: flex-end;
        margin: 5px 0;
        .delete-cancel{
          margin-right: 5px;
        }
        .delete-confirm{
          margin-right: 5px;
        }
      }
      /deep/.d-card__body {
        height: 40%;
      }
    }
  }
}
@media screen and(min-width: 800px){
  .part-left {
    flex-basis: 300px;
  }
}

</style>