<template>
  <d-card
    class="item-card"
    :class="[{'shaking': isShaking}]"
    :body-style="{ padding: '0' }"
    shadow="never"
  >
    <div class="remind-item" @dblclick="openCurrentRemind">
      <header class="item-header" slot="header">
        <span class="item-from" :title="item.msg.userName">{{ $t('remind.from') + '：' + item.msg.userName }}</span>
        <d-icon 
          class="delete-icon" 
          icon="d-icon-dustbin-outline" 
          size="inherit" 
          :title="$t('remind.deleteRemind')" 
          @click="deleteRemind(item.reminderId,index)" 
          @mouseover.native="isShaking = true"
          @mouseleave.native="isShaking = false"
          ></d-icon>
      </header>
      <div class="item-content">
        <div class="item-details" :title="getMsgContent(item.msg)">{{ getMsgContent(item.msg) }}</div>
        <span class="item-time">{{ item.reminderSendTime | timeFilter('yyyy-MM-dd hh:mm') }}</span>
      </div>
      <!-- <div class="item-button">
        <d-button type="danger" size="mini" @click="deleteRemind(item.reminderId,index)">{{$t('remind.delete')}}</d-button>
      </div> -->
    </div>
  </d-card>
</template>

<script>
import Filters from './RemindFilters';
export default {
  name: 'remind-item',
  filters: {
    timeFilter: Filters.timeFilter
  },
  props: {
    item: Object,
    index: [String, Number]
  },
  data() {
    return {
      isShaking: false
    };
  },
  methods: {
    // 删除消息提示
    deleteRemind(Id,index){
      this.$emit('deleteRemind', {Id, index});
    },
    // 双击打开当前消息
    openCurrentRemind() {
      this.$emit('openCurrentRemind', this.item);
    },
    // 获取消息内容
    getMsgContent (val){
      if(val.msgType === 9){ //网页链接消息
        return val ? val.url : `[${this.$t('remind.unknown')}]`;
      }else if(val.msgType === 89){//視頻消息
        return val ? `[${this.$t('remind.microVideoMessage')}]` : `[${this.$t('remind.unknown')}]`;              // vedioFileName
      }else if(val.msgType === 6){//文件消息
        return val ? `[${$t('remind.file')}]：${val.fileName}` : `[${this.$t('remind.unknown')}]`;
      }else if(val.msgType === 3){//语音消息
        return val ? `[${this.$t('remind.voiceMessage')}]` : `[${this.$t('remind.unknown')}]`;
      }else {
        return val ? val.body : `[${this.$t('remind.unknown')}]`; // 文字消息
      }
    },
  }
}

</script>
<style lang='scss' scoped>
.item-card {
  &:hover {
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1),-3px 0px 6px rgba(0, 0, 0, 0.1);
  }
  &:not(:last-child) {
    margin-bottom: 8px;
  }
  &.shaking {
    animation: shake 1s infinite;
    .remind-item .item-header {
      background: linear-gradient(to right, #e53935, #e35d5b);
    }
  }
  .remind-item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  overflow: hidden;
  font-size: 14px;
  .item-header {
    width: 100%;
    padding: 8px;
    background: linear-gradient(to left, #487FFF, #467DFE);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    transition: 1s;
    .item-from {
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 0;
      flex: 1;
      cursor: default;
      user-select: none;
    }
    .delete-icon {
      color: #fff;
      cursor: pointer;
    }
  }
  .item-content {
    padding: 8px;
    user-select: none;
    display: flex;
    flex-direction: column;
    flex: 1;
    .item-details {
      font-size: 14px;
      flex: 1;
      overflow: hidden;
      white-space: normal;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box; 
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      color: #303133;
    }
    .item-time {
      font-size: 12px;
      color: #C0C4CC;
      margin-top: 5px;
      align-self: flex-end;
    }
  }
}

  @keyframes shake {
    from,to {
      transform: translate3d(0, 0, 0);
    }
    10%,30%,50% {
      transform: translate3d(-2px, 0, 0);
    }
    20%,40% {
      transform: translate3d(2px, 0, 0);
    }
  }
}
</style>
