<template>
  <div class="message-remind">
    <part-left @ViewDetails='ViewDetails'></part-left>
    <part-right @showAddRemind='showAddRemind' @shoWeek='shoWeek'></part-right>
  </div>
</template>

<script>

import PartLeft from "./PartLeft";
import PartRight from "./PartRight";
export default {
  name: "remind-me",
  components: {
    PartRight,
    PartLeft,
  },
  data() {
    return {
      isShowModel:false,
      remindList:''
    }
  },
  methods:{
    showAddRemind(pickedDate) {
      this.isShowModel = true
      console.log('pickedDate',pickedDate)
    },
    ViewDetails(val) {
      console.log('this.remindList',val)
      this.$emit('toAppValue',val)
    },
    shoWeek(data,time) {
      console.log(time)
      this.$emit('shoWeekPage',time)
    }
  }
};
</script>

<style lang="scss">
  .message-remind{
    display: flex;
    height: 100%;
    width: 100%;
  }
</style>

